import $ from 'jquery';
import 'select2';
import 'magnific-popup';

const CustomDropdown = $.fn.select2.amd.define("ModalDropdown",
	[
		"select2/utils",
		"select2/dropdown",
		"select2/dropdown/attachBody",
		"select2/dropdown/attachContainer",
		"select2/dropdown/search",
	],
	function (Utils, Dropdown, AttachBody, AttachContainer, Search) {
		// Here goes the code of this custom adapter
		let dropdownWithSearch = Utils.Decorate(Dropdown, Search);
	}
);

export default {
	init() {
		$('.select').select2({
			width : "resolve",
			dropdownAdapter : CustomDropdown,
			dropdownParent: $('#modal .modal__body')
		});

		$('.select').on('select2:opening', function(e) {
			console.log('asfs');
			$.magnificPopup.open({
				items: {
					src: '#modal', // can be a HTML string, jQuery object, or CSS selector
					type: 'inline'
				}
			});
		})
	}

};


